  cursor: default;
  opacity: 0.5;
}

.slick-slider .slick-arrow:before {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) scale(1);
  transform: translate(-50%, -50%) scale(1);
  -webkit-transform-origin: left;
  transform-origin: left;
  transition: all 0.3s;
}

